<template>

  <div style="text-align: center">
    <h1>{{ user.userNickname }}'s Profile</h1>
    <b-card 
      style ="width: 400px; display:inline-block"
    >
      <!-- 간단하게 userinfo에서 변경가능하도록 -->
      <b-card-title>이름</b-card-title>
      <div class="form-control" v-if="userNameOn==1">{{ user.userName }}</div>
      <b-input v-if="userNameOn==0"></b-input>
      <b-button :pressed.sync="userNameOn">이름 수정</b-button>
      
      <hr>
      <b-card-title>아이디</b-card-title>
      <div class="form-control" v-if="userIdOn==1">{{ user.userId }}</div>
      <b-input v-if="userIdOn==0"></b-input>
      <b-button :pressed.sync="userIdOn">아이디 수정</b-button>

      <hr>
      <b-card-title>닉네임</b-card-title>
      <div class="form-control" v-if="userNicknameOn==1">{{ user.userNickname }}</div>
      <b-input v-if="userNicknameOn==0"></b-input>
      <b-button :pressed.sync="userNicknameOn">닉네임 수정</b-button>

      <hr>
      <b-card-title>이메일</b-card-title>
      <div class="form-control" v-if="userEmailOn==1">{{ user.userEmail }}</div>
      <b-input v-if="userEmailOn==0"></b-input>
      <b-button :pressed.sync="userEmailOn">이메일 수정</b-button>

      <hr>
      <b-card-title>전화번호</b-card-title>
      <div class="form-control" v-if="userPhonenumberOn==1">{{ user.userPhonenumber }}</div>
      <b-input v-if="userPhonenumberOn==0"></b-input>
      <b-button :pressed.sync="userPhonenumberOn">전화번호 수정</b-button>

      <hr>
      <span @click="toUserUpdate">정보 수정</span>
      <br>
      <br>
      <!-- 회원 탈퇴는 관리자만 가능 -->
      <b-card-text @click="toDelete" v-if="$store.state.userpermission == 1">회원 탈퇴</b-card-text> 
    </b-card> 
    
  </div>

</template>

<script>
// 유저가 접근하는 것
// 관리자가 접근하는 것

export default {
  data() {
    return {
      user: {},
      id: this.$route.params.id || this.$store.state.userindex,
      update: null,
      userNameOn: true,
      userEmailOn: true,
      userIdOn: true,
      userNicknameOn: true,
      userPhonenumberOn: true,
    }
  },
  methods: {
    getUser(){
      this.$axios.get("/user/read/" + this.id).then((res) => {
        console.log(res.data.id)
        this.update = res.data.id
        this.user = res.data
      })
      .catch((error) => {
        console.log(error);
      })
    },
    toUserUpdate() {
      this.$router.push({name: 'userUpdate', params: {index: this.update}})
    },
    toDelete() {
      this.$axios.delete("/user/delete/" + this.id).then((res) => {
        alert("삭제되었습니다.")
        this.$router.push("/userlist")
      })
    },
    changeNameOn(){
      this.userNameOn = 0;
    },
    changeNameOff(){
      this.userNameOn = 1;
    }
  },
  mounted() {
    this.getUser();
  },
};
</script>