<template>
  <div style="text-align: center">
    <h1>게시글 수정</h1>
    <b-card 
      style ="width: 1000px; display:inline-block">
      <b-card-title>
        제목
        <input 
          class="form-control"
          type="text"
          maxlength="20"
          v-model="boardTitle"/> 
      </b-card-title>
      <hr>
        내용
        <input 
        class="form-control"
        type="text"
        maxlength="20"
        v-model="boardContents"/>
      <hr>
      <button type="submit" class="mt-4 btn-pers" id="login_button" @click="boardUpdate" >
        게시글 업데이트
      </button>
    </b-card>
  </div>

</template>

<script>
export default {
  data() {
    return {
      boardId: this.$route.params.id,
      boardTitle: null,
      boardContents: null,
    }
  },
  methods: {
    boardUpdate() {
      const formData = new FormData();
      formData.append("id", this.boardId)
      formData.append("boardTitle" , this.boardTitle)
      formData.append("boardContents" , this.boardContents)

      try {
        this.$axios.put("/board/update", formData).then((res)=> {
          console.log(res.data);
          this.$router.push({name: 'boardInfo' , params: {id: res.data.id}})

        }) 
        } catch(error) {
            console.log(errror)
      }
    }
  }
      
}
</script>